.tooltip {
  position: absolute;
  top: -40px;
  background: #d7d7d7;
  border: 1px solid black;
  border-radius: 7px;
  padding: 3px 10px;
  color: black;
  max-width: 300px;
  white-space: nowrap;
}

.tooltip::after {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #d7d7d7;
  position: absolute;
  transform: rotate(41deg);
  top: 22px;
  right: 5px;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}
