.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.5);
}

.modal {
  width: 700px;
  background: #f4f4f4;
  border-radius: 7px;
  box-shadow: 0 0 5px;
  margin-top: 100px;
}

.alarmtext {
  text-align: center;
}

.buttoncontainer,
.imgcontainer {
  display: flex;
  justify-content: space-around;
  margin: 0px 0px 10px 0px;
  padding: 0px 200px;
}

.alarmimg {
  margin: 0px 0px 20px 0px;

  width: 50px;
  animation: alarm 2s;
  animation-iteration-count: infinite;
}

.button {
  padding: 5px 30px;
}

.button:active {
  transform: scale(0.9);
}

.button:hover {
  background: #ccc;
}

.containerDark {
  background: rgba(0, 0, 0, 0.7);
}

.modalDark {
  background: rgb(68, 68, 68);
}

@keyframes alarm {
  0% {
    transform: rotate(-35deg);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-35deg);
  }
}
