.todoContainer {
  position: relative;
  height: 28px;
  margin: 0px 0px 3px 0px;
}

.name {
  margin: 0px 5px 0px 5px;
  display: inline-block;
}

.rememberTime {
  display: inline-block;
  width: 162.32px;
  position: absolute;
  right: 53px;
  letter-spacing: 1.3px;
  font-size: 13.9px;
  top: 8.7px;
}

.buttons {
  position: absolute;
  top: 5px;
  right: 0;
  display: flex;
  justify-content: space-between;
  width: 52px;
}

.inputEditTodo {
  width: 947px;
  margin: 0px 5px 0px 4px;
  font-size: 15px;
  padding: 0;
  height: 100%;
  letter-spacing: 0.26px;
}

.icon {
  position: relative;
  top: 7px;
}

.dataTimeinput {
  height: 100%;
  border-radius: 7px;
  cursor: pointer;
}

.checked {
  opacity: 0.8;
  text-decoration: line-through;
}

.disabledButton {
  cursor: default;
  pointer-events: none;
  opacity: 0.3;
}

.button {
  cursor: pointer;
}

.label,
.label input {
  cursor: pointer;
}
.checkTodoContainer,
.dateTimeContainer,
.removeTodoContainer,
.saveTodoContainer,
.editTodoContainer {
  position: relative;
}

.dateTimeContainer,
.checkTodoContainer {
  display: inline-block;
}
