.button {
  margin-right: 3px;
  width: 40px;
  height: 25px;
}

.buttonAll {
  width: 50px;
}

.button:hover {
  background: #ccc;
}

.button.active {
  background: #9e9e9e;
  transform: scale(0.95);
  box-shadow: 0 0 2px;
  font-weight: bold;
  font-size: 1.05rem;
}
.container {
  display: flex;
  justify-content: space-between;
}

.filterContainer {
  margin-left: 5px;
  display: flex;
  align-items: center;
}
