.button {
  margin-right: 5px;
  width: 30px;
  height: 30px;
}

.button:hover {
  background: #ccc;
}

.button:active {
  transform: scale(0.95);
  box-shadow: 0 0 1.5px;
}

.button.active {
  background: #9e9e9e;
  transform: scale(0.9);
  box-shadow: 0 0 2px;
  font-weight: 900;
  font-size: 1.2rem;
  line-height: 0;
}
