.todoContainer {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  flex: 0 0 auto;
  margin: 0px 0px 10px 0px;
}

.todoList {
  list-style-type: none;
  margin: 0;
  padding: 0 0 20px;
  flex: 1 0 auto;
}
