* {
  box-sizing: border-box;
  transition: 0.15s linear;
}

.appcontainer {
  width: 1200px;
  margin: 0 auto;
  height: 100%;
  position: relative;
  padding: 45px 0 20px;

  display: flex;
  flex-direction: column;
}

body {
  height: 100%;
}

html {
  height: 100%;
}

#root {
  height: 100%;
}

button {
  cursor: pointer;
  border-radius: 5px;
}

.background {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.light {
  background-color: white;
}

.dark {
  background-color: #444444;
  color: #c1c1c1;
}

.dark button {
  background: #5f5f5f;
  color: #e1e1e1;
}

.dark button:hover {
  color: black;
}

.dark input {
  background: #969696;
  border-color: #ffffff;
}

.paginator {
  width: 100%;
  display: flex;
  align-items: center;

  margin: 0 auto;

  flex: 0 0 auto;
}
