.input,
.button {
  height: 30px;
  border-radius: 7px;
}
.buttonRemove {
  position: absolute;
  right: 0;
  top: -3px;
  font-weight: bold;
}

.input {
  width: 1075px;
  margin-right: 5px;
  font-weight: 600;
}

.input:focus {
  outline: #ccc solid 2px;
}

.button {
  width: 120px;
  transition: 0.2s linear;
}

.button:hover {
  background: #ccc;
}

.button:active {
  transform: scale(0.95);
  box-shadow: 0 0 2px;
}

.label {
  position: relative;
  height: 35px;
  margin-top: 40px;
}

.datetime {
  top: 0;
  border-radius: 7px;
  height: 25px;
  position: absolute;
  right: 125px;
  transition: 0.7s linear;
  cursor: pointer;
}
.datetime:focus {
  outline: 2px solid #ccc;
  box-shadow: 0 0 3px;
  font-weight: bold;
}

.header {
  margin: 0px 0px 10px 0px;
}

.body {
  height: 30px;
  position: relative;
  margin: 0px 0px 5px 0px;
}

.remindMeLater {
  margin: 0px 0px 0px 5px;
  font-size: 18px;
  position: relative;
  top: -5px;
}

.checkReminder {
  cursor: pointer;
}

.dateTimeContainer {
  display: inline-block;
}

